export interface ISurveyDataContext {
    loadType: string;
    groupCode: string;
    idleThreshold: number;
    visitorSessionId: string;
    expirationDays?: number | undefined;
    refire?: boolean;
}

interface ISurveyQuestion {
    Id: string;
    Answer: string;
    Control?: HTMLInputElement | HTMLTextAreaElement;
}

interface ISurveyRequestContainer {
    VisitorSessionId: string;
    Questions: ISurveyQuestion[];
}

export type SurveyInitializerReceiver = (
    createRequest: (form: HTMLFormElement) => Promise<Response>
) => void;

export default class SurveyController {
    private context: ISurveyDataContext;
    private initializerReceiver: SurveyInitializerReceiver;
    private fireIdle: boolean;
    private fireSurvey: boolean;
    private mouseLeave: boolean;

    constructor(
        context: ISurveyDataContext,
        initializerReceiver: SurveyInitializerReceiver
    ) {
        this.context = context;
        this.initializerReceiver = initializerReceiver;

        this.fireIdle = true;
        this.fireSurvey = true;
        this.mouseLeave = false;

        this.handleSurveyFire();
        this.handleCookieByButtonEvent();
    }

    private handleSurveyFire(): void {
        const refire = this.context.refire ?? false;

        refire
            ? this.handleLoadTypeByContext()
            : this.cancelSurveyFireIfCookieExists();
    }

    public async submitSurvey(form: HTMLFormElement): Promise<Response> {
        const request: ISurveyRequestContainer = {
            VisitorSessionId: this.context.visitorSessionId,
            Questions: this.createQuestionsContainer(form)
        };

        const response = await fetch("/Shared/Services/SurveyService.ashx", {
            method: "POST",
            body: new URLSearchParams({ survey: JSON.stringify(request) })
        });

        if (
            document.cookie.indexOf(
                `${this.context.groupCode}-surveySubmitted=`
            ) === -1
        ) {
            const expireDays = this.context.expirationDays ?? 1;

            this.setCookie(
                `${this.context.groupCode}-surveySubmitted`,
                "1",
                expireDays
            );
        }

        return new Promise<Response>((resolve, reject) => resolve(response));
    }

    private createQuestionsContainer(form: HTMLFormElement): ISurveyQuestion[] {
        const data = new FormData(form);

        const controls = Array.from(form.elements) as (
            | HTMLInputElement
            | HTMLTextAreaElement
        )[];

        const questions: ISurveyQuestion[] = [];

        data.forEach((value, key) => {
            questions.push({
                Id: key,
                Answer: value.toString(),
                Control: controls.find(
                    (control) => control.name === key && control.value === value
                )
            });
        });

        questions
            .filter((question) => question.Id.match(/OTHER/gi))
            .forEach((question) => {
                const key = question.Id.split("-").pop();

                questions.splice(questions.indexOf(question), 1);

                if (!key) return;

                const relative = questions.find(
                    (relative) =>
                        relative.Control?.id.match(/OTHER/gi) &&
                        relative.Control?.name === key
                );
                if (!relative) return;

                relative.Answer = `${relative.Answer} - ${question.Answer}`;

                questions.splice(questions.indexOf(relative), 1, relative);
            });

        questions.forEach((question) => delete question.Control);

        return questions.filter((question) => question.Answer.trim() !== "");
    }

    private removeCookie(): void {
        document.cookie = `${this.context.groupCode}-surveySubmitted=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;
    }

    private setCookie(cname: string, cvalue: string, exdays: number): void {
        const date = new Date();
        date.setTime(date.getTime() + exdays * 24 * 60 * 60 * 1000);

        const expires = `expires=${date.toUTCString()}`;
        document.cookie = `${cname}=${cvalue};${expires};path=/`;
    }

    private handleCookieByButtonEvent(): void {
        const hasExpirationDays = this.context.expirationDays === undefined;

        const candidates = Array.from(
            document.querySelectorAll("#AcceptOfferButton, [name=createOrder]")
        );

        candidates.forEach((candidate) => {
            candidate.addEventListener("click", (event) => {
                hasExpirationDays
                    ? this.removeCookie()
                    : this.setCookie(
                          `${this.context.groupCode}-surveySubmitted`,
                          "1",
                          this.context.expirationDays as number
                      );
            });
        });
    }

    public surveyAlreadySubmitted(): boolean {
        return (
            document.cookie.indexOf(
                `${this.context.groupCode}-surveySubmitted=`
            ) !== -1
        );
    }

    private cancelSurveyFireIfCookieExists(): void {
        if (this.surveyAlreadySubmitted()) return;

        this.handleLoadTypeByContext();

        addEventListener("CancelSurveyFire", (event) =>
            this.setFireStatusToFalse()
        );
    }

    private handleLoadTypeByContext(): void {
        switch (this.context.loadType) {
            case "Timeout":
                this.handleTimeout();
                break;
            case "Idle":
                this.handleIdle();
                break;
            case "ExitIntent":
                this.handleExitIntent();
                break;
            case "IdleExitIntent":
                this.handleIdleExitIntent();
                break;
            case "OnLoad":
                this.handleOnLoad();
                break;
        }
    }

    private handleTimeout(): void {
        this.handleIdleThreshold(() => {
            if (this.fireSurvey)
                this.initializerReceiver(this.submitSurvey.bind(this));
        });
    }

    private handleIdleThreshold(response: () => void): void {
        if (isNaN(this.context.idleThreshold)) return;

        setTimeout(response, this.context.idleThreshold * 1000);
    }

    private handleIdle(): void {
        this.setFireStatusToFalseByElementCapture("form input");
        this.addIdleEvent();
    }

    private setFireStatusToFalseByElementCapture(selector: string): void {
        Array.from(document.querySelectorAll(selector)).forEach((input) => {
            ["click", "change", "keyup", "focus"].forEach((event) => {
                input.addEventListener(event, this.setFireStatusToFalse);
            });
        });
    }

    private handleExitIntent(): void {
        this.addExitEvent(true);
    }

    private handleIdleExitIntent(): void {
        this.setFireStatusToFalseByElementCapture("form");
        this.addIdleEvent();
        this.addExitEvent(false);
    }

    private handleOnLoad(): void {
        this.initializerReceiver(this.submitSurvey.bind(this));
    }

    private addIdleEvent(): void {
        this.handleIdleThreshold(() => {
            if (this.fireIdle) {
                this.initializerReceiver(this.submitSurvey.bind(this));
                this.setFireStatusToFalse();
            }
        });
    }

    private setFireStatusToFalse(): void {
        this.fireSurvey = false;
        this.fireIdle = false;
    }

    private addExitEvent(checkHeight: boolean): void {
        document.documentElement.addEventListener(
            "mouseleave",
            (event) => {
                if (this.mouseLeave) return;

                if (!checkHeight || event.clientY < 20) {
                    if (this.fireSurvey) {
                        this.initializerReceiver(this.submitSurvey.bind(this));
                        this.setFireStatusToFalse();
                    }

                    this.mouseLeave = true;
                }
            },
            false
        );
    }
}
